/* Global styling */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  body {
    font-family: 'Arial', sans-serif;
    background-color: #f4f4f9;
    color: #333;
  }
  
  /* Container for the whole page */
  .not-found-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background: linear-gradient(to bottom right, #4e73df, #36a2eb); /* Beautiful gradient background */
    color: #fff;
  }
  
  /* Centered content */
  .not-found-content {
    text-align: center;
    background-color: rgba(255, 255, 255, 0.1); /* Semi-transparent white background */
    padding: 30px 50px;
    border-radius: 10px;
    max-width: 600px;
    width: 100%;
  }
  
  /* Title styling */
  .not-found-title {
    font-size: 6rem;
    font-weight: 700;
    margin-bottom: 10px;
    letter-spacing: -2px;
    text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.2);
  }
  
  /* Message styling */
  .not-found-message {
    font-size: 1.5rem;
    margin-bottom: 20px;
    line-height: 1.5;
  }
  
  /* Sub-message styling */
  .not-found-submessage {
    font-size: 1.2rem;
    margin-bottom: 30px;
    font-weight: 400;
  }
  
  /* Button styling */
  .back-home-button {
    font-size: 1rem;
    color: #fff;
    background-color: #36a2eb;
    padding: 15px 30px;
    text-decoration: none;
    border-radius: 30px;
    display: inline-block;
    transition: all 0.3s ease;
    text-transform: uppercase;
    font-weight: 600;
  }
  
  .back-home-button:hover {
    background-color: #4e73df;
    transform: translateY(-3px); /* Slightly lift on hover */
  }
  
  .back-home-button:active {
    transform: translateY(1px); /* Button presses down slightly */
  }
  